html {
  background-image: url('../../resource/wallpaper_dark\ 1.svg');
  background-repeat: no-repeat;
  transition: .4s ease;
}

#login-main-div {
  width: calc(100% - 60px);
  height: calc(100% - 30%);
  background-color: rgb(33, 40, 50, 0.5);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  max-width: 430px;
  backdrop-filter: blur(20px);
  border-radius: 30px
}

#ZenCore-icon {
  width: 100%;
  height: 220px;
  background-image: url('../../resource/ZenCore.svg');
  background-size: 200px;
  background-repeat: no-repeat;
  background-color: transparent;
  background-position: center;
}

#account-input{
  width: calc(100% - 50px);
  height: 92px;
  left: 50%;
  transform: translateX(-50%);
  position: absolute;
  background-color: transparent;
}

#input-ZenID {
  background-color: rgb(33, 40, 50, 0.4);
  border-radius: 10px;
  width: calc(100% - 15px);
  height: 40px;
  border: none;
  box-shadow: 0px 0px 30px -15px #31475e;
  position: absolute;
  padding-left: 15px;
  outline: none;
  color: rgb(245, 245, 245, 1);
  font-size: 17px;
  font-family: 'GSansMedium';
  transition: 0.8s;
}

#input-Password {
  background-color: rgb(33, 40, 50, 0.4);
  border-radius: 10px;
  width: calc(100% - 108px - 15px);
  height: 40px;
  border: none;
  box-shadow: 0px 0px 30px -15px #31475e;
  position: absolute;
  padding-left: 15px;
  outline: none;
  color: rgb(245, 245, 245, 1);
  font-size: 17px;
  margin-top: 50px;
  font-family: 'GSansMedium';
  transition: 0.8s;
} #input-Password-ChangeType {
  background-color: rgb(33, 40, 50, 0.4);
  border-radius: 10px;
  width: 98px;
  height: 42px;
  border: none;
  box-shadow: 0px 0px 30px -15px #31475e;
  position: absolute;
  padding-left: 15px;
  outline: none;
  color: rgb(245, 245, 245, 1);
  font-size: 17px;
  right: 0px;
  bottom: 0px;
  background-image: url('../../resource/icon/mdi_hide.svg');
  background-size: 30px;
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
}

#input-Submit {
  width: 100%;
  height: 40px;
  background-color: rgb(33, 40, 50, 0.5);
  position: absolute;
  top: 120px;
  border-radius: 10px;
  border: none;
  box-shadow: 0px 0px 30px -15px #31475e;
  font-size: 17px;
  font-family: 'GSansMedium';
  color: #F5F5F5;
  transition: 0.5s;
  cursor: pointer;
  outline: none
}
#input-Submit:hover {
  background-color: rgb(33, 40, 50, 0.8);
  box-shadow: 0px 0px 30px -10px #31475e;
  transition: 0.5s
}

#account-input input::placeholder {
  font-family: 'GSansRegular';
  font-size: 17px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: rgb(245, 245, 245, 0.2);
  filter: blur(0px);
  transition: 0.8s;
}
#account-input input:focus::placeholder {
  font-family: 'GSansRegular';
  font-size: 17px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: rgb(245, 245, 245, 0);
  filter: blur(80px);
  transition: 1.8s
}

#getZenID {
  font-size: 17px;
  color: #007AFF;
  text-decoration: underline;
  text-decoration-thickness: 2px;
  font-family: 'GSansMedium';
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  user-select: none

}


