html {
  font-family: 'GSansThin';
}

._container {
  width: unset;
  height: unset;
  background-color: rgb(33, 40, 50, 0.6);
  border-radius: 20px;
  backdrop-filter: blur(50px);
  position: absolute;
}

#home-main-div {
  width: calc(100% - 100px);
  height: 100%;
  background-color: transparent;
  position: absolute;
  top: 50px;
  overflow-y: scroll;
  left: 50%;
  transform: translateX(-50%);
  max-width: 350px
}

#div-profile {
  width: 100%;
  aspect-ratio: 1/1;
  position: relative;
}

#popup-selector {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 9999;
  background-color: transparent;
  top: 0px;
  left: 0px;
  display: none
}

#popup-alert-box {
  width: calc(100% - 50px);
  height: 300px;
  background-color: rgb(33, 40, 50, 0.6);
  position: fixed;
  border-radius: 20px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
  backdrop-filter: blur(50px);
  box-shadow: 0px 0px 30px -10px  rgb(49, 71, 94, 0.8);
}
.selector-button {
  position: relative;
  width: calc(100% - 20px);
  height: 50px;
  background-color: rgb(33, 40, 50, 0.5);
  border-radius: 15px;
  margin-top: 10px;
  margin-bottom: -5px;
  margin-left: 10px;
  cursor: pointer;
  overflow: hidden;
} .selectorButton-title {
  color: #F5F5F5;
  font-family: 'GSansMedium';
  font-size: 17px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 15px
}

.selectorButton-arrow.show {
  width: 30px;
  height: 30px;
  background-image: url('../../resource/icon/right.svg');
  background-color: transparent;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 5px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: 0.8s;
  filter: blur(0px);
  opacity: 1
} .selectorButton-arrow.hide {
  width: 30px;
  height: 30px;
  background-image: url('../../resource/icon/right.svg');
  background-color: transparent;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 5px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: 1.8s;
  filter: blur(30px);
  opacity: 0
}

.filter-blur-layer {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  backdrop-filter: blur(30px);
  background-color: rgb(33, 40, 50, 0.1);
}

#div-selector {
  width: 100%;
  height: 50px;
  left: 0px;
  position: relative;
  margin-top: 20px;
  border-radius: 100px
}

#selector-mobile-icon{
  position: absolute;
  width: 35px;
  height: 35px;
  background-image: url('../../resource/icon/right.svg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgb(33, 40, 50, 0);
  border-radius: 100px;
  transition: 0.5s;
} #selector-mobile-icon:hover {
  background-color: rgb(33, 40, 50, 0.8);
}

#selector-mobile-category {
  color: #F5F5F5;
  font-family: 'GSansBlack';
  font-size: 20px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 25px
}
#selector-mobile-view {
  cursor: pointer;
  width: 100%;
  height: 100%;
  position: absolute;
  border-radius: 100px;
  background-color: rgb(33, 40, 50, 0);
  border: none
}
#selector-desktop-view {
  display: none;
}

#div-mainCategory-title {
  display: none;
}

#div-mainCategory {
  width: 100%;
  height: 500px;
  margin-top: 80px;
  overflow-y: scroll;
}
::-webkit-scrollbar {
  display: none;
}




#divProfile-image { 
  width: 40%;
  aspect-ratio: 1/1;
  background-image: url('../../resource/ZenCore.svg');
  background-size: cover;
  background-color: #212832;
  border-radius: 10px;
  position: absolute;
  top: 15px;
  left: 15px;
  box-shadow: 0px 0px 30px -10px #31475E
}
#divProfile-userInfo {
  position: absolute;
  left: 15px;
  top: unset;
  background-color: transparent;
}
#user-name{
  font-family: 'GSansBold';
  font-size: 28px;
  color: #F5F5F5;
}
#user-license{
  font-family: 'GSansBold';
  font-size: 17px;
  color: RGB(245, 245, 245, 0.5);
}

#divProfile-userInfo-separator {
  margin-top: 15px
}
#user-email{
  font-family: 'GSansMedium';
  font-size: 15px;
  color: RGB(245, 245, 245, 0.5);
}
#user-phone{
  font-family: 'GSansMedium';
  font-size: 15px;
  color: RGB(245, 245, 245, 0.5);
}

